<template>
    <el-container style="height: 500px; border: 1px solid #eee">
        <el-header class="el-icon-s-home" style=" font-size: 40px; background-color:#409EFF; color:azure">
            教考系统</el-header>
        <el-footer>
            <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
                <el-menu-item index="1"><router-link to="/student" tag="span">账号信息</router-link></el-menu-item>
                <el-menu-item index="2"><router-link to="/course" tag="span">课程表</router-link></el-menu-item>
                <el-menu-item index="3"><router-link to="/studentExam" tag="span">参加考试</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/score" tag="span">考试成绩查询</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/courseSelection" tag="span">选课</router-link></el-menu-item>
                <el-menu-item index="5"><router-link to="/studentLogin" tag="span">退出登录</router-link></el-menu-item>
            </el-menu>
        </el-footer>


        <el-main v-model="teacherData">
            <el-table :data="tableData" style="width: 100%">
                <el-table-column prop="schemeNum" label="考试编号" width="200">
                </el-table-column>
                <el-table-column prop="schemeBegin" label="开始时间" width="200">
                </el-table-column>
                <el-table-column prop="schemeEnd" label="结束时间" width="200">
                </el-table-column>
                <el-table-column prop="examNum" label="试卷编号" width="200">
                </el-table-column>
                <el-table-column prop="course.courseName" label="考试课程" width="200">
                </el-table-column>
                <el-table-column prop="examType.typeName" label="考试类型" width="200">
                </el-table-column>
                <el-table-column label="操作" width="200">
                    <template slot-scope="scope">
                        <el-button type="text" @click="gradingExam(scope.row.schemeNum)">查看</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-main>
    </el-container>
</template>

<script>
import axios from 'axios';

export default {
    props: ['id'],
    data() {
        return {
            tableData: [],
            studentID: '',

        }
    },
    mounted() {
        const storedID = sessionStorage.getItem('studentID');
        this.studentID=storedID;
        this.getAll();


    },
    methods: {
        //根学号查询学生表
        getAll() {
            axios.get(`/api/examScheme/studentId/${this.studentID}`).then((result) => {
                this.tableData = result.data.data;
            });
        },
        //跳转阅卷页面
        gradingExam(examNum) {
            this.$router.push({ name: 'joinExam', params: { id:Number(examNum) } });
        },
        
    },
}
</script>

<style scoped>
.el-container {
    min-height: 100vh; /* 至少占据整个视口高度 */
    max-height: 1200px; /* 最大高度限制 */
    overflow-y: auto; /* 如果内容超出高度，显示滚动条 */
    display: flex;
    flex-direction: column;
  }
  
  .el-header, .el-footer {
    flex: 0 0 auto; /* 固定高度 */
    background-color: #409EFF;
    color: azure;
    padding: 10px;
  }
  
  .el-main {
    flex: 1 1 auto; /* 自动占据剩余空间 */
    overflow-y: auto; /* 如果内容超出高度，显示滚动条 */
    padding: 20px;
    box-sizing: border-box;
  }
</style>